@import "variables";

.app {
  width: 100%;
  padding: 8px;
  margin: 0 auto;
  color: $fontColor;

  @media (min-width: 480px) {
    min-width: 480px;
    max-width: 560px;

  }

  &__todos {
    background-color: $whiteColor;
    margin-bottom: $marginBottom;
    padding: 16px;
    border-radius: 8px;
    box-shadow: 8px 8px 8px 0 rgba(34, 60, 80, 0.5);

    @media (min-width: 480px) {
      padding: 32px;
    }
  }
}
