@import "../variables";

.todo-header {
  margin: 0;
  margin-bottom: $marginBottomSmallScreen;

  @media (min-width: 480px) {
    margin-bottom: $marginBottom;
  }

  &__title {
    text-align: center;
    font-size: $titleSizeSmallScreen;
    font-weight: 700;
    color: $whiteColor;
    text-shadow: 8px 8px 8px rgba(34, 60, 80, 0.5);

    @media (min-width: 480px) {
      font-size: $titleSize;
    }
  }
}
